@import "src/scss/normalize";

.container {
  @include container;
  padding: 40px 0 40px 0;
}

.titleBlock {
  display: flex;
  align-items: flex-end;
  justify-content: space-between;
  width: 100%;
}

.name {
  min-width: 160px;
  font-weight: 700;
  font-size: 36px;
  line-height: 37px;
  color: $black;
  margin-bottom: 0;
}

.description {
  min-width: 160px;
  font-size: 18px;
  line-height: 22px;
  font-weight: 500;
  margin-bottom: 0;
}

//.lang {
//  min-width: 160px;
//  font-weight: 700;
//  font-size: 16px;
//  color: $grey;
//  writing-mode: vertical-lr;
//  transform: rotate(-180deg);
//  margin-bottom: 0;
//}

.imgBlock {
  margin-top: 40px;
  height: 600px;
  overflow: hidden;
}

.img {
  width: 100%;
}

@media (max-width: 940px) {
  .container {
    padding-right: 30px;
    padding-left: 30px;
  }
}

@media (max-width: 767px) {
  .container {
    padding: 61px 30px 104px 30px;
  }

  .titleBlock {
    display: block;
  }

  //.lang {
  //  display: none;
  //}

  .imgBlock {
    height: auto;
  }
}